const HASURA_GRAPHQL_ENGINE_HOSTNAME = 'auth.mysetgoals.com';

const scheme = (proto) => {
  return window.location.protocol === 'https:' ? `${proto}s` : proto;
};

export const GRAPHQL_URL = `${scheme(
  'http'
)}://${HASURA_GRAPHQL_ENGINE_HOSTNAME}/v1/graphql`;

export const REALTIME_GRAPHQL_URL = `${scheme(
  'ws'
)}://${HASURA_GRAPHQL_ENGINE_HOSTNAME}/v1/graphql`;

export const authClientId = 'mC1Rxn6SumCfLK0PoBMkZ9MjGfNuNTbb';
export const authDomain = 'setmygoals-dev.au.auth0.com';
export const callbackUrl =
  !process.env.NODE_ENV || process.env.NODE_ENV === 'development'
    ? 'http://localhost:3000'
    : `https://www.mysetgoals.com`;
